import { useCallback, useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom"
import { Fn } from "../../../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../../../models/baseModel";
import { WorkflowRunService } from "../../../../../services/taskService";
import { MyBox } from "../../../../core/MyBox";
import { MyButton } from "../../../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../../../core/TableModel";
import TableComponent from "../../../../core/TableNewComponent";
import { Text } from "../../../../core/Text";
import { UIFormatDateTimeVNComponent } from "../../../../core/UI_FormatComponent";

export default function IndexComponent() {
    const projectKey: string | undefined = useParams().projectKey;
    const workflowKey: string | undefined = useParams().workflowKey;
    let navigate = useNavigate();

    const column: any = [
        new TableColumn({ title: "STT", key: "stt", render: (data: any, row: any, index: number) => Fn.RenderSTT(data, row, index, pageInfo) }),
        new TableColumn({ title: "Tên", key: "name", render: (data: any, row: any) => <MyButton type="link" onClick={() => { handleClickDetail(row.key) }} value={data} /> }),
        new TableColumn({ title: "Tên Workflow", key: "workflow", render: (data: any) => <Text>{data.name}</Text> }),
        new TableColumn({ title: "Ngày thực hiện", key: "date", render: (data: any) => <UIFormatDateTimeVNComponent value={data} /> }),
        new TableColumn({ title: "Ngày tạo", key: "createdAt", render: (data: any) => <UIFormatDateTimeVNComponent value={data} /> }),
        new TableColumn({ title: "Ngày cập nhật", key: "updatedAt", render: (data: any) => <UIFormatDateTimeVNComponent value={data} /> }),
    ];
    const [isLoading, setIsLoading] = useState(false);
    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);

    const loadData = useCallback(() => {
        setIsLoading(true);
        var paginationRequest: PaginationRequest = new PaginationRequest(1, 20, new OrderByModel());
        WorkflowRunService.GetList(paginationRequest, projectKey, workflowKey)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setPageInfo(new PageInfoModel(rs));
                    setData(rs.data.pagedData);
                }
            })
            .finally(() => setIsLoading(false))
    }, [projectKey, workflowKey])

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleClickDetail(key: string) {
        navigate(`/tools/tasks/projects/${projectKey}/workflows/${workflowKey}/run/${key}/detail`);
    }

    return (
        <MyBox
            title="Workflow run"
            isLoading={isLoading}
            buttonList={() => <Link to={`/tools/tasks/projects/${projectKey}/workflows/${workflowKey}/run/add`}><MyButton type="primary" value="Thêm mới" /></Link>}>
            <TableComponent
                isLoading={isLoading}
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                loadData={loadData}
                dataSource={data}
                column={column} />
        </MyBox>
    )
}